* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.carousel-container {
  width: 100%;
  height: auto;
  /* border: 1px solid red; */
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 70px 0;
}

.carousel-container .h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 10vh;
  justify-content: space-evenly;
  /* text-align: center; */
  animation: attention 4s ease infinite;
}
.border {
  border-bottom: 5px solid #22397e;
  width: 6rem;
  border-radius: 5rem;
}
@keyframes attention {
  9% {
    transform: none;
  }
  12% {
    transform: scale(1.05);
  }
  16% {
    transform: scale(0.95);
  }
  20% {
    transform: scale(1.03);
  }
  24% {
    transform: scale(1);
  }
}

.button {
  border-radius: 5px;
  background-color: #777573;
  border: none;
  color: #ffffff;
  text-align: center;
  /* font-size: 28px; */
  /* padding: 20px; */
  width: 50%;
  height: 5vh;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
}
.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}
.button span a {
  text-decoration: none;
  color: #ffffff;
}

.button span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.button:hover span {
  padding-right: 25px;
}

.button:hover span:after {
  opacity: 1;
  right: 0;
}
.read-more {
